<template>
    <div v-bind="fullScreenParentProps">
        <ul class="dzs-code-header-box" v-if="showMenu">
            <li>
                <div></div>
                <ol class="hidden-scrollbar" :style="{ 'max-height': codeHight }">
                    <li @click="copy"></li>
                    <li @click="openSearch">
                        <span></span>
                        <span>Ctrl + F</span>
                    </li>
                </ol>
            </li>
            <li>
                <div></div>
                <ol class="hidden-scrollbar" :style="{ 'max-height': codeHight }">
                    <li v-for="(item, index) in themeList" :key="index" @click="changeTheme(item, index)">
                        <span>{{ item.label }}</span>
                        <i class="el-icon-check" v-if="item.cheked"></i>
                    </li>
                </ol>
            </li>
            <li>
                <div></div>
                <ol class="hidden-scrollbar" :style="{ 'max-height': codeHight }">
                    <li v-for="(item, index) in languageList" :key="index" @click="changeLanguage(item, index)">
                        <span>{{ item.label }}</span>
                        <i class="el-icon-check" v-if="item.value.toLowerCase() == mode.toLowerCase()"></i>
                    </li>
                </ol>
            </li>
            <li>
                <div></div>
                <ol class="hidden-scrollbar" :style="{ 'max-height': codeHight }">
                    <li @click="() => fullCoder = !fullCoder">
                        <span></span>
                        <i class="el-icon-check" v-if="fullCoder"></i>
                    </li>
                </ol>
            </li>
        </ul>
        <div class="code-editor-cust full-screen-child">
            <textarea ref="textarea"></textarea>
            <span @click="nullTipClick" class="null-tip" :class="{ 'null-tip-hidden': hasCode }" :style="nullTipStyle">
                {{ placeholderShow }}
            </span>
            <div class="code-editor-cust-footer"> {{ mode }}</div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">

/**
 * 
 * @author zs.duan
 * @date 2024-07-26
 * @props {string} value 
 * @props {string} language 
 * @props {string} placeholder 
 * @props {string} zIndex z-index
 * @props {string} autoHeight 
 * @props {string} height 
 * @props {object} editorOptions 
 * @props {boolean} showMenu 
 * @methods {function}copy 
 * */ 

import copy from "../../tool/copy";
// 
import _CodeMirror from 'codemirror'

// 
import 'codemirror/lib/codemirror.css'
//  options  darcula  gruvbox-dark hopscotch  monokai
import 'codemirror/theme/panda-syntax.css'; // 
import 'codemirror/theme/erlang-dark.css';  // 
import 'codemirror/theme/juejin.css'; //
import 'codemirror/theme/paraiso-dark.css'; // 
import 'codemirror/theme/xq-light.css'; // xq-
import 'codemirror/theme/xq-dark.css'; // xq-
import 'codemirror/theme/3024-night.css'; // 3024-
import 'codemirror/theme/3024-day.css'; // 3024-
import 'codemirror/theme/duotone-dark.css'; // 
import 'codemirror/theme/duotone-light.css'; // 


//css
import "codemirror/addon/hint/show-hint.css";

// js
import "codemirror/addon/hint/show-hint.js";
import "codemirror/addon/hint/javascript-hint.js";
import "codemirror/addon/hint/sql-hint.js";
import "codemirror/addon/hint/xml-hint.js";
import "codemirror/addon/hint/anyword-hint.js";
import "codemirror/addon/hint/css-hint.js";
import "codemirror/addon/hint/html-hint.js";

//  vue  JS  JS 
import 'codemirror/mode/javascript/javascript.js'
import 'codemirror/mode/css/css.js'
import 'codemirror/mode/xml/xml.js'
import 'codemirror/mode/clike/clike.js'
import 'codemirror/mode/markdown/markdown.js'
import 'codemirror/mode/python/python.js'
import 'codemirror/mode/r/r.js'
import 'codemirror/mode/shell/shell.js'
import 'codemirror/mode/sql/sql.js'
import 'codemirror/mode/swift/swift.js'
import 'codemirror/mode/vue/vue.js'

// 
import 'codemirror/addon/fold/foldgutter.css'
import 'codemirror/addon/fold/foldcode'
import 'codemirror/addon/fold/foldgutter'
import 'codemirror/addon/fold/brace-fold'
import 'codemirror/addon/fold/comment-fold'

// 
import 'codemirror/addon/scroll/annotatescrollbar.js'
import 'codemirror/addon/search/matchesonscrollbar.js'
import 'codemirror/addon/search/match-highlighter.js'
import 'codemirror/addon/search/jump-to-line.js'

import 'codemirror/addon/dialog/dialog.js'
import 'codemirror/addon/dialog/dialog.css'
import 'codemirror/addon/search/searchcursor.js'
import 'codemirror/addon/search/search.js'

// 
const CodeMirror = window.CodeMirror || _CodeMirror

import { themeList, languageList } from "./config";
export default {
    name: 'dzsCode',
    props: {
        // 
        value: {
            type: String,
            default: ''
        },
        // 
        language: {
            type: String,
            default: "javascript"
        },
        placeholder: {
            type: String,
            default: null
        },
        // z-index
        zIndex: {
            type: [Number, String],
            default: 999
        },
        autoHeight: {
            type: [String, Boolean],
            default: true
        },
        // 
        height: {
            type: Number,
            default: 0
        },
        editorOptions: {
            type: Object,
            default: () => { }
        },
        /***/
        showMenu: {
            type: Boolean,
            default: true
        },
        /***/ 
        showHint: {
            type: Boolean,
            default: false
        },
    },
    data() {
        return {
            code: '',
            hasCode: false,
            mode: 'javascript', // 
            coder: null,// 
            // 
            options: {
                // 
                tabSize: 4,
                indentUnit: 2, //  2
                theme: 'panda-syntax',
                line: true,
                smartIndent: true, //
                lineWrapping: true, // 
                readOnly: false, // 
                lineNumbers: true, // 
                styleActiveLine: true, // 
                showCursorWhenSelecting: true, // 
                matchBrackets: true, //   
                extraKeys: { 'Ctrl': 'autocomplete', },//
                hintOptions: {
                    tables: {
                        users: ['name', 'score', 'birthDate'],
                        countries: ['name', 'population', 'size']
                    },
                    completeSingle: false
                },
                foldGutter: true, // 
                gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter', 'CodeMirror-lint-markers'],
            },
            fontSize: "18px",
            //  JS 
            // code  
            fullCoder: false,
            themeList: themeList,
            languageList: languageList,
            codeHight: '0px',
            codeId: "dzsCode" + new Date().getTime(),
            isFirst : false,
        }
    },
    watch: {
        value:{
            handler(val) {
                if(this.isFirst) return
                if(this.coder && val){
                    this.coder.setValue(val);
                    this.isFirst = true
                }
            },
            deep: true,
            immediate : true
        },
        language: {
            immediate: true,
            handler(language) {
                this._getCoder().then(() => {
                    // 
                    if (language) {
                        // 
                        let modeObj = this._getLanguage(language)
                        // 
                        if (modeObj) {
                            this.mode = modeObj.label
                            this.coder.setOption('mode', `text/${modeObj.value}`)
                        }
                    }
                })
            }
        }
    },
    computed: {
        placeholderShow() {
            if (this.placeholder == null) {
                return ``
            } else {
                return this.placeholder
            }
        },
        nullTipStyle() {
            if (this.options.lineNumbers) {
                return { left: '46px' }
            } else {
                return { left: '12px' }
            }
        },
        // coder 
        coderOptions() {
            return Object.assign(this.options, this.editorOptions)
        },
        isAutoHeight() {
            let { autoHeight } = this;
            if (this.height > 0) return false;
            autoHeight = true;
            return autoHeight;
        },
        fullScreenParentProps() {
            let props = {
                class: {
                    'full-screen-parent': true,
                    'full-screen': this.fullCoder,
                    'auto-height': this.isAutoHeight,
                },
                id: this.codeId,
                style: {}
            }
            if (this.fullCoder) {
                props.style['z-index'] = this.zIndex
            }
            if (!this.isAutoHeight && !this.fullCoder) {
                props.style['height'] = this.height + 'px'
            }
            return props
        }
    },
    mounted() {
        this._initialize();
    },
    methods: {
        /***/
        _initialize() {
            // 
            this.coder = CodeMirror.fromTextArea(this.$refs.textarea, {
                ...this.coderOptions,
            })
            // 
            if (this.value || this.code) {
                this.hasCode = true
                this.setCodeContent(this.value || this.code)
            } else {
                this.coder.setValue('')
                this.hasCode = false
            }
            // 
            this.coder.on('change', (coder) => {
                this.code = coder.getValue()
                if (this.code) {
                    this.hasCode = true
                } else {
                    this.hasCode = false
                }
                if (this.$emit) {
                    this.$emit('input', this.code)
                }
            })
            this.coder.on('focus', () => {
                this.hasCode = true
            })
            this.coder.on('blur', () => {
                if (this.code) {
                    this.hasCode = true
                } else {
                    this.hasCode = false
                }
            })

            this.coder.on('cursorActivity', () => {
                this.showHint && this.coder.showHint()
            })

            //
            const resizeObserver = new ResizeObserver(entries => {
                for (let entry of entries) {
                    const { height } = entry.contentRect;
                    this.codeHight = (height - 35) + 'px';
                }
            })
            resizeObserver.observe(document.querySelector(`#${this.codeId}`));
        },
        setCodeContent(val) {
            setTimeout(() => {
                if (!val) {
                    this.coder.setValue('')
                } else {
                    this.coder.setValue(val)
                }
            }, 300)
        },
        /***/ 
        _getLanguage(language) {
            // 
            return this.languageList.find((mode) => {
                // 
                let currentLanguage = language.toLowerCase()
                let currentLabel = mode.label.toLowerCase()
                let currentValue = mode.value.toLowerCase()

                //  java  x-java  value  label 
                return currentLabel === currentLanguage || currentValue === currentLanguage
            })
        },
        _getCoder() {
            let _this = this
            return new Promise((resolve) => {
                (function get() {
                    if (_this.coder) {
                        resolve(_this.coder)
                    } else {
                        setTimeout(get, 10)
                    }
                })()
            })
        },
        /***/ 
        changeMode(val) {
            // 
            this.coder.setOption('mode', `text/${val}`)
            // 
            let label = this._getLanguage(val).label.toLowerCase()

            // 
            this.$emit('language-change', label)
        },
        nullTipClick() {
            this.coder.focus()
        },
        /**
         * 
         * @param {Object} item 
         * @param {Number} index 
         * */
        changeTheme(item, index) {
            this.coder.setOption('theme', item.value);
            this.themeList.forEach(item => {
                item.cheked = false;
            })
            this.themeList[index].cheked = true;
        },
        /***/
        changeLanguage(item, index) {
            this.changeMode(item.value);
            this.mode = item.value;
        },
        /***/
        openSearch() {
            if (!this.hasCode) {
                this.$message({
                    message: '',
                    type: 'warning'
                });
                return;
            }
            this.coder.execCommand('find') //
        },
        /***/
        copy() {
            if (!this.hasCode) return;
            copy(this.code)
        }
    }
}
</script>

<style lang="scss">
.dzs-code-header-box {
    display: flex;
    background-color: #59595a;
    padding: 2px;

    >li {
        font-size: 16px;
        padding: 4px 10px;
        color: #fff;
        user-select: none;
        cursor: pointer;
        position: relative;

        &:hover {
            background-color: rgba(0, 0, 0, 0.2);

            ol {
                display: block;
            }
        }
    }

    ol {
        position: absolute;
        display: none;
        width: 120px;
        z-index: 9999;
        background-color: #fff;
        color: #59595a;
        border-radius: 4px;
        box-shadow: 0 0 12px 0 rgba(46, 72, 233, 0.2);
        left: 0;
        overflow-y: auto;

        li {
            padding: 5px 8px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            &:hover {
                background-color: rgba(0, 0, 0, 0.5);
                color: #fff;
            }
        }
    }
}

.code-editor-cust {
    flex-grow: 1;
    display: flex;
    position: relative;
    height: 100%;

    .CodeMirror {
        flex-grow: 1;
        z-index: 1;

        .CodeMirror-code {
            // line-height: 24px;
            font-size: v-bind(fontSize);

            span {
                font-size: v-bind(fontSize);
            }
        }

        .CodeMirror-linenumber {
            font-size: v-bind(fontSize);
        }
    }

    .code-mode-select {
        position: absolute;
        z-index: 2;
        right: 10px;
        top: 10px;
        max-width: 130px;
    }

    .CodeMirror {
        height: auto;
        min-height: 100%;
    }

    .null-tip {
        position: absolute;
        top: 4px;
        left: 36px;
        z-index: 10;
        color: #e0dfdf;
        line-height: initial;
        font-size: v-bind(fontSize);
    }

    .null-tip-hidden {
        display: none;
    }

    .code-editor-cust-footer {
        position: absolute;
        bottom: 3px;
        right: 5px;
        z-index: 999;
        color: #e0dfdf;
    }

    /***/
    // .CodeMirror-selected {
    //     min-height: 24px !important;
    // }
}

/*  */
.full-screen-parent {
    position: relative;

    .full-screen-icon {
        opacity: 0;
        color: black;
        width: 20px;
        height: 20px;
        line-height: 24px;
        background-color: white;
        position: absolute;
        top: 2px;
        right: 2px;
        z-index: 9;
        cursor: pointer;
        transition: opacity 0.3s;
    }

    &:hover {
        .full-screen-icon {
            opacity: 1;

            &:hover {
                background-color: rgba(255, 255, 255, 0.88);
            }
        }
    }

    &.full-screen {
        position: fixed;
        top: 10px;
        left: 10px;
        width: calc(100% - 20px);
        height: calc(100% - 20px);
        padding: 10px;
        background-color: #f5f5f5;

        .full-screen-icon {
            top: 12px;
            right: 12px;
        }

        .full-screen-child {
            height: calc(100% - 35px);
            max-height: 100%;
            min-height: 100%;
        }
    }

    .full-screen-child {
        height: calc(100% - 35px);
    }

    &.auto-height {
        .full-screen-child {
            min-height: 150px;
            max-height: 320px;
            height: unset;
            overflow: hidden;
        }

        &.full-screen .full-screen-child {
            height: calc(100% - 35px);
            max-height: 100%;
            min-height: 100%;
        }
    }

}
</style>